const Noticia = (props) => {

     const {description, name, url} = props.info;
      
     let desc = description.text;

     if(desc ){
          desc = desc.substr(0, 200);
     }

     

     return(
          <div className="card  border-success ">
          <h4 className="card-title">{name.text}</h4>
        <a href={url} target="_blank" className=" list-group-item card text-white mb-3">
             
             <p className="">{desc}</p>
        </a>
        </div>
       
     );


}

export default Noticia;